import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '@/components/Home'
import TempoTraining from '@/components/TempoTraining'

Vue.use(VueRouter)

export default new VueRouter({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/tempo-training',
      name: 'TempoTraining',
      component: TempoTraining
    }
  ]
})



